.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $regular;
    text-transform: uppercase;
    margin: 1rem 0;
    font-size: 1.2rem;

    &.error{
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }
}   

table {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    
    thead {
        background-color: $grisOscuro;
        padding-bottom: 20rem;
        
    
        th {
            color: $blanco;
            padding: .5rem;
            text-align: center;
            

        }
    }
    tbody {
        td {
            padding: 1rem 0 0 0;
            text-align: center;

            &:last-of-type {
                width: 20rem;
            }

            .boton-amarillo-block,
            .boton-rojo-block {
                margin: 0.5rem 0;
                width: 100%;
            }

        .imagen-tabla{
            width: 15rem;
            height: 10rem;
            margin: 0 auto;

   
            }
        }
    }
}

.imagen-pequeña{
    width: 20rem;
}

hr {
    margin: 10rem 0;
}