.footer {
    padding: calc($separacion / 2) 0 0.3rem 0;
    background-color: $grisOscuro;
    margin-bottom: 0;
    margin-top: 6rem;
    padding: 3rem 0;


    .contenido-footer {
        padding-top: calc($separacion / 2) 0 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .navegacion {
        visibility: visible;
        opacity: 1;
    }

    .copyright {
        color: $blanco;
        text-align: center;
        margin-bottom: 0;
        padding-bottom: 0;
        padding-top: 1.5rem;
        font-size: 1.2rem;
    }
}

