.formulario {
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0 ;
    }
    legend {
        font-size: 2rem;
        color: $grisOscuro;
    }
    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid  $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    //remover los botones de los input number
    input[type=number]::-webkit-inner-spin-button,
    input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
    }

    textarea{
        height: 20rem;
    }

    .forma-contacto {
        max-width: 80rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="radio"] {
        margin: 0 auto;
        width: auto;
    }
}

.reducir {
    width: 70%;
    margin: 0 auto;
    @include tablet {
        width: 30%;
    }
  
    input[type="password"], input[type="text"] {
        margin-bottom: 0;
        max-width: 100%;
    }
    .forma-contacto {
        align-items: center;
        position: relative;
        width: 100%;
        margin: 0;
    }
}
.imgPass {
    width: 2.5rem;
    height: 2rem;
    position: absolute;
    right: 1rem;

    &:hover{
        cursor: pointer;
    }
}