.dark-Mode {
    background-color: darken($grisOscuro, 10);

    p {
        color: $blanco;
    }

    blockquote, a, h1, h2, h3, h4 {
        color: $gris;
    }

    .anuncio {
        background-color: $grisOscuro;
        border: 1px solid darken($gris, 60);

        .precio {
            color: $verde;
        }

        .icono {
            filter: invert(100%);
        }

        p {
        color: $gris;
        }
    }
    

    .formulario {
        p, label, legend {
            color: $gris;
        }

        input:not([type="submit"]),
        textarea,
        select {
            background-color: $grisOscuro;
            border: 1px solid $grisOscuro;
            color: $blanco;
        }
    }

    .imgPass, .boton-gris-block{
        filter: invert(100%);
    }

    tr{
        color: $blanco;
    }
    thead {
        filter: invert(100%);
    }
}