@include tablet {
    .contenedor-anuncios{
        @include grid(3, 2rem);
    }
}

.anuncio {
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris, 5);
    margin-bottom: 2rem;
    overflow: hidden;


    picture {
        height: 30rem;
        
          img{
              height: 100%;
              object-fit: cover; //Para evitar que la imagen se distorsione
          }
      }
    .contenido-anuncio{
        padding: 2rem;
    }
    .contenido-anuncio > p{
        height: 10rem;
        overflow: hidden;
        white-space: wrap;
      

    }

    h3, p {
        margin: 0;
    }

    h3{
        font-weight: $bold;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;

    }
}

p.precio{
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
    padding-top: 2rem;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li {
        flex: 1;
        display: flex;
        align-items:  center;


        img {
            flex: 0 0 2rem;
            margin-right: 0.3rem;
        }
        p {
            font-weight: $black;
        }
        
    }
}
.no-fondo{
    background-color: $blanco;
    border: none;
    margin-bottom: 2rem;
}