  margin-top: 2.5rem;
  display: block;
  border: none;
  height: 4rem;
  line-height: 2rem;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
