  background-color: #262626;
  cursor: pointer;
}

.contener-boton {
  width: 100%;
}
@media (min-width: 768px) {
  .contener-boton {
    width: 25%;
  }
}

.dark-Mode {
  background-color: #1a1a1a;
}
.dark-Mode p {
  color: #FFFFFF;
}
.dark-Mode blockquote, .dark-Mode a, .dark-Mode h1, .dark-Mode h2, .dark-Mode h3, .dark-Mode h4 {
  color: #e1e1e1;
}
.dark-Mode .anuncio {
  background-color: #333333;
  border: 1px solid #484848;
}
.dark-Mode .anuncio .precio {
  color: #71B100;
}
.dark-Mode .anuncio .icono {
  filter: invert(100%);
}
.dark-Mode .anuncio p {
  color: #e1e1e1;
}
.dark-Mode .formulario p, .dark-Mode .formulario label, .dark-Mode .formulario legend {
  color: #e1e1e1;
}
.dark-Mode .formulario input:not([type=submit]),
.dark-Mode .formulario textarea,
.dark-Mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark-Mode .imgPass, .dark-Mode .boton-gris-block {
  filter: invert(100%);
}
.dark-Mode tr {
  color: #FFFFFF;
}
.dark-Mode thead {
  filter: invert(100%);
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #d4d4d4;
  margin-bottom: 2rem;
  overflow: hidden;
}
.anuncio picture {
  height: 30rem;
}
.anuncio picture img {
  height: 100%;
  object-fit: cover;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio .contenido-anuncio > p {
  height: 10rem;
  overflow: hidden;
  white-space: wrap;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio h3 {
  font-weight: 700;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

p.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
  padding-top: 2rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
  align-items: center;
}
.iconos-caracteristicas li img {
  flex: 0 0 2rem;
  margin-right: 0.3rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-fondo {
  background-color: #FFFFFF;
  border: none;
  margin-bottom: 2rem;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
  margin-left: 1rem;
  margin-right: 1rem;
  line-height: 1.3;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
  margin-left: 1rem;
  margin-right: 1rem;
}

.footer {
  padding: 2.5rem 0 0.3rem 0;
  background-color: #333333;
  margin-bottom: 0;
  margin-top: 6rem;
  padding: 3rem 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 0;
  padding-bottom: 0;
  padding-top: 1.5rem;
  font-size: 1.2rem;
}

.loginFormat {
  margin: 17rem auto;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario input[type=number]::-webkit-inner-spin-button,
.formulario input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 80rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0 auto;
  width: auto;
}

.reducir {
  width: 70%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .reducir {
    width: 30%;
  }
}
.reducir input[type=password], .reducir input[type=text] {
  margin-bottom: 0;
  max-width: 100%;
}
.reducir .forma-contacto {
  align-items: center;
  position: relative;
  width: 100%;
  margin: 0;
}

.imgPass {
  width: 2.5rem;
  height: 2rem;
  position: absolute;
  right: 1rem;
}
.imgPass:hover {
  cursor: pointer;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.3;
  max-width: 60rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada {
  margin-top: 2rem;
}
.texto-entrada h4 {
  text-align: start;
}
@media (min-width: 768px) {
  .texto-entrada {
    margin-top: 0;
  }
}
.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.4rem;
  background-color: #71B100;
  margin-top: 0.5rem;
}

.barra {
  display: grid;
  grid-template-rows: 1fr, 1fr, 1fr;
  row-gap: 3rem;
  align-items: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    grid-template-columns: 3fr 3fr 1fr;
    column-gap: 2rem;
    justify-content: space-between;
    align-items: center;
  }
}

.izquierda {
  grid-row: 2/3;
  margin: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .izquierda {
    align-items: center;
    grid-column: 1/2;
    grid-row: 1/2;
  }
}

.centro {
  grid-row: 3/4;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .centro {
    align-items: center;
    grid-column: 2/3;
    grid-row: 1/2;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-column: 1/2;
  order: 0;
}
@media (min-width: 768px) {
  .derecha {
    flex-direction: row;
    align-items: flex-end;
    grid-column: 3/4;
    padding-right: 1.5rem;
  }
}
.derecha .dark-mode-boton {
  width: 5rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-bottom: 0;
    margin-left: 2rem;
    width: 3rem;
    order: 2;
  }
}
.derecha .sesionBtn {
  width: 5rem;
  margin-top: 1rem;
  margin-bottom: 3rem;
}
.derecha .sesionBtn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .sesionBtn {
    margin-bottom: 0;
    margin-top: 0;
    margin-left: 4rem;
    width: 3rem;
    order: 1;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: flex-start;
    opacity: 1;
    visibility: visible;
    margin-bottom: 0;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  position: relative;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.mobile-menu {
  position: relative;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

.testimonial {
  background-color: #71B100;
  font-size: 2rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  height: 6rem;
  width: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
  top: -1rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
  font-size: 1.6rem;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 400;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.2rem;
}
.alerta.error {
  background-color: rgb(216, 38, 38);
}
.alerta.exito {
  background-color: #71B100;
}

table {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table thead {
  background-color: #333333;
  padding-bottom: 20rem;
}
table thead th {
  color: #FFFFFF;
  padding: 0.5rem;
  text-align: center;
}
table tbody td {
  padding: 1rem 0 0 0;
  text-align: center;
}
table tbody td:last-of-type {
  width: 20rem;
}
table tbody td .boton-amarillo-block,
table tbody td .boton-rojo-block {
  margin: 0.5rem 0;
